import React from 'react'
import style from './hunterStyle.module.css'
import { useState, useEffect } from 'react'
import Reset from './Reset'
import Result from './Result'
import Popup from './Popup'
import {hunterDB} from '../db/HunterDB'



let hunter = JSON.parse(JSON.stringify(hunterDB))

let resultSkills = []

