import React from 'react'
import style from './hunterStyle.module.css'


const Popup = (props) =>{

    return(
    <div className={ props.isPopup ? style.popup__container : `${style.popup__container} ${style.active}`}>
        <div style={{width: '400px',
            height: '200px',
            backgroundColor: 'white',
            border: '1px solid rgba(0,0,0,0.5)',
            padding: '20px',
            position: 'relative'}}>
            <h1>Это не финальная версия прокачки в игре!</h1>
            
            <span style={{position: 'absolute', 
            right: '10px', 
            top: '5px', 
            display: 'block', 
            border: '2px solid rgba(0,0,0,0.5)',
            width: '15px',
            textAlign: 'center',
             cursor: 'pointer'}} onClick={e => props.setIsPopup(false)}>X</span>

        </div>
    </div>
    )
}

export default Popup