import React from 'react'
import style from './hunterStyle.module.css'

const Result = (props) =>{
    return(
        <div className={style.result__container}>
                <div>
                <div style={{padding: '5px'}}>Потрачено очков навыка: {props.count}</div>
                {props.resultSkills?.map((v,i) => {
                    if(v.info === ''){
                        return ''
                    }
                    return(<div key={i} 
                        style={{border: '1px solid rgba(0,0,0,0.5)', margin: '5px', borderRadius: '10px', padding: '5px'}}>
                        {v.info} - Итог: {v.bonus}
                        </div>)
                })}
                </div>
        </div>
    )
}

export default Result