import React from 'react'
import { Link } from "react-router-dom";


const Header = (props) =>{
    return(
        <div className="App">
              <div className='header'>
                <ul>
                  <li><Link to='combat'>Бой</Link></li>
                  <li><Link to='hunter'>Охотник</Link></li>
                  <li><Link to='master'>Мастер</Link></li>
                  <li><Link to='medic'>Медик</Link></li>
                  <li><Link to='survival'>Выживание</Link></li>
                  <li><Link to='stalker'>Сталкер</Link></li>
                </ul>
              </div>
          </div>
    )
}

export default Header;